@import "../../styles/helpers/_helpers";

.works {
    margin-bottom: 40px;
    .title {
        position: absolute;
        content: '';
        top: -16px;
        background-color: #fff;
        padding: 0 20px;
        font-family: 'Open Sans Condensed',sans-serif;
        letter-spacing: 3px;
        text-transform: uppercase;
        font-size: 28px;
        margin: 0;
    } 
    &__my {
        font-family: Neuton;
        text-transform: uppercase;
        background-color: yellowgreen;
        padding: 5px 20px;
    }
    &__inner {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
    }
    &__item {
        flex: 0 0 auto;
        color: #818181;
        padding: 5px 10px;
        text-align: center;
        width: 180px;
        border: 2px solid rgba(255, 240, 0, 0.2);
        margin: 4px;
        &:hover {
            border: 2px solid rgba(0, 0, 0, 0.1);
        }
    }
}
